export const philosophie = ["Toujours baigné dans le sport, l'associatif et les activités ludiques, j'ai multiplié les expériences bénévoles, que ce soit dans le football, à l'école, et aujourd'hui dans le handball ou les jeux de société. J'ai pu ainsi mesurer de près les problématiques rencontrées au quotidien. Tout en mesurant l'importance de ces organisations et leur nécessité dans la vie de tous les jours, pour alié le bien-être, l'échange, la convivialité, le respect des autres...", "Ce système est en danger et doit aussi se repenser face à la société d'aujourd'hui. Je n'ai pas attendu mon licenciement économique pour œuvrer et abonder dans ce sens. Tout reste à faire. La volonté est présente. Et ma motivation grandissante pour faire de cette philosophie une entreprise à part entière et à temps plein."]
export const competences = "Que ce soit au contact des enfants, des collectivités ou du monde sportif professionnel, j'ai pu développer diverses compétences dans l'animation, le management, la communication, l'écriture ou encore la gestion de projets et d'évènements. C'est aujourd'hui autant d'atouts que je veux mettre à la disposition des clubs et associations, au travers de ma société. A commencer par mon esprit de recherche, ma capacité de synthèse, mon sens de l'organisation et mon aisance rédactionnelle. J'ai envie, dans un premier temps, de proposer à mes futurs clients des ressources et un soutien papier pour leur projet. Ainsi qu'un support écrit pour argumenter, présenter et vendre leur activité ou discipline."
export const prestations = "Aujourd'hui, la priorité est de donner les moyens aux clubs et associations de se structurer et s'organiser. C'est pourquoi, dans le cadre de mon projet, je veux avant tout proposer des services rédactionnels : écriture projet associatif à partir d'un audit de la structure, réalisation de plaquettes commerciales, montage de dossiers d'aides diverses et fiches de recrutement. Autant de prestations rémunérées à part fixe au départ, puis en pourcentage de la subvention dans un second temps. L'idée étant à terme que puisse se débrouiller sans moi ou pas pour son démarchage. Il n'est pas question de les lier définitivement à ma société."
export const finalite = "Dans un second temps, je vise à créer une association autour de l'économie sociale et solidaire. Et l'idée d'entraîner tout le mouvement sportif amateur sarthois dans une démarche commune de mutualisation. Sous forme par exemple d'un groupement d'employeurs, au service des différentes structures. Mais aussi, avec du mécénat, des adhésions de parents et la collaboration des collectivités et transporteurs, j'aimerais mettre en place des facilités de déplacements et une organisation collective, à la sortie des écoles, pour permettre aux enfants de rejoindre leurs activités, prendre leur goûter et faire leurs devoirs. En imaginant notamment des connexions avec les jeunes retraités..."